import React, { Component } from 'react';

class SideMenu extends Component {
    constructor(props) {
        super(props);
        this.state = {
        }
    }
    
    componentDidMount() {
    }

    componentDidUpdate() {
    }

    render() {
        return (
            <div className='side-menu'>
                <div className="side-menu-options" >
                    <div>
                        <label htmlFor="sort">Sort by</label>
                        <div>
                            <select id="sort" onChange={this.props.sortlist}>
                                <option value="name">name</option>
                                <option value="ageString">age</option>
                                <option value="random">random</option>
                                <option value="adoptionFeeString">fee</option>
                                <option value="birthDate">birth date</option>
                                <option value="sizeCurrent">current size</option>
                                <option value="sizePotential">potential size</option>
                            </select>
                        </div>
                    </div>
                    <div>
                        <label htmlFor="order">Order</label>
                        <div>
                            <select id="order" onChange={this.props.orderlist}>
                                <option value="+">Ascending</option>
                                <option value="-">Descending</option>
                            </select>
                        </div>
                    </div>
                </div>
                <ul className="side-menu-list">
                    {this.props.animals.map((animal)=>{
                        return <li className="side-menu-li" key={animal.id} onClick={()=>{return this.props.sendnewreq(animal.id)}}>{animal.id}</li>
                    })}
                </ul>
            </div>
        )
    }
}

export default SideMenu;