import React, { useEffect, useState } from 'react';
import { initializeApp } from 'firebase/app';
import {
  getAuth,
  onAuthStateChanged,
  GoogleAuthProvider,
  signInWithPopup,
  signOut,
} from 'firebase/auth';
import { BrowserRouter, Routes, Route } from "react-router-dom";
import Main from './components/main';  
import Home from './components/home';
import Nav from './components/nav';

// /public/animals/search/available/
// dogs/?limit=10&page=2&sort=+distance=kilometers&fields[animals]=name&include=fosters
function App() {
    const [userName, setUserName] = useState("");
    const [logined, setLogined] = useState(false);
    const [user, setUser] = useState({});
    const [menuToggled, setMenuToggled] = useState(false);

    useEffect(() => {
      // console.log ("mounted and updated!");
        initFirebaseAuth();
    }, [])

    function handlsMenu() {
      console.log ("hi");
      if (menuToggled) {
        setMenuToggled(false)
      }
      else {
        setMenuToggled(true)
      }
  }
    async function signIn() {
        // Sign in Firebase using popup auth and Google as the identity provider.
        var provider = new GoogleAuthProvider();
        await signInWithPopup(getAuth(), provider);
      }

    function signOutUser() {
      console.log("functioning");
        // Sign out of Firebase.
        signOut(getAuth());
    }

    function getUserName() {
        return getAuth().currentUser.displayName;
    }

    function initFirebaseAuth() {
        // Listen to auth state changes.
        onAuthStateChanged(getAuth(), authStateObserver);
    }

    function authStateObserver(user) {
        if (user) {
          const name = getUserName();
          setUserName(name);
          setLogined(true);
          setUser(user);
        } 
        else {
            setUserName('Stranger');
            setLogined(false);
            setUser({});
        }
    }
    
    return (
      // BrowserRouter, Routes, Route
        <div className="app">
          <BrowserRouter>
            <Nav handlsMenu={handlsMenu} menuToggled={menuToggled} username={userName} logined={logined}/>
            <Routes>
              <Route path="/" element={<Home />}></Route>
              <Route path="/main" element={<Main menuToggled={menuToggled}/>}></Route>
            </Routes>
            <div style={{display: 'flex', justifyContent:"center", alignItems: 'center'}}> <a href="https://github.com/moonsol124/clone-website-top">https://github.com/moonsol124/clone-website-top</a> </div>
          </BrowserRouter>
        </div>
    );
}

export default App;


