import uniqid from 'uniqid';
import React, { useState, useEffect} from 'react';
import Details from './details';

function Item(props) {
    const [detailToggled, setDetailToggled] = useState(false);

    function handlesClick(e) {
        if (detailToggled) {
            setDetailToggled (false);
        }
        else {
            setDetailToggled (true);
        }
    }

    useEffect(() => {
    }, [detailToggled])
    
    return (
    <>
        <div className="card"> 
            {(!detailToggled) ? <>
            <img src={props.item.url} alt={props.item.Name} className="card-img"/>
            <div className="card-texts">
                <h3>Information</h3>
                <div className='card-text'>
                    <p>{props.item.Name}</p>
                    <p>{props.item.Age}</p>
                    <p>{props.item.Breed}</p>
                    <p>{props.item.Fee}</p>
                </div>
                <div className='card-btn-group'>
                    <button onClick={handlesClick}> View details </button>
                    <button> Send messages </button>
                </div>
            </div></> :
            <Details item={props.item} handlesclick={handlesClick}/>}
        </div>
    </>
    );
}

export default Item;