import React, { Component } from 'react';
import SideMenu from './sidemenu';
import List from './list';

class Main extends Component {
    constructor(props) {
        super(props);
        this.state = {
            loading: true,
            animals: [{'id': 'dogs'}, 
            {'id': 'cats'},
            {'id': 'birds'},
            {'id': 'alpacas'},
            {'id': 'chickens'},
            {'id': 'chinchillas'},
            {'id': 'cows'},
            {'id': 'degus'},
            {'id': 'donkeys'},
            {'id': 'ducks'},
            {'id': 'ferrets'},
            {'id': 'fish'},
            {'id': 'frogs'},
            {'id': 'geckos'},
            {'id': 'geese'},
            {'id': 'gerbils'},
            {'id': 'goats'},
            {'id': 'groundhogs'},
            {'id': 'guineapigs'},
            {'id': 'hedgehogs'},
            {'id': 'hermitcrabs'},
            {'id': 'horses'},
            {'id': 'iguanas'},
            {'id': 'lizards'},
            {'id': 'llama'},
            {'id': 'mice'},
            {'id': 'otters'},
            {'id': 'pigs'},
            {'id': 'ponies'},
            {'id': 'prairiedogs'},
            {'id': 'rabbits'},
            {'id': 'rats'},
            {'id': 'sheep'},
            {'id': 'skunks'},
            {'id': 'snakes'},
            {'id': 'sugargliders'},
            {'id': 'tarantulas'},
            {'id': 'tortoises'},
            {'id': 'turkeys'},
            {'id': 'turtles'},
            ],
            items: [],
            sort: 'animals.name',
            currentAnimal: '',
            order: '+',
            sideToggled: false,
            windowSize: window.screen.width,
            // url: "",
        }

        this.sendNewReq = this.sendNewReq.bind(this);
        this.sortList = this.sortList.bind(this);
        this.orderList = this.orderList.bind(this);
    }
    
    componentDidMount() {
        this.sendNewReq('dogs');
        this.setState({
            currentAnimal: 'dogs',
        })
        if (this.state.windowSize <= 800) {
            this.setState({
                sideToggled: true,
            })
        }
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (this.state.sort !== prevState.sort) {
            this.sendNewReq(this.state.currentAnimal);
        }

        if (this.state.order !== prevState.order) {
            this.sendNewReq(this.state.currentAnimal);
        }
        console.log(this.state.sideToggled);
    }

    sortList(e) {
        if (e.target.value === "random") {
            this.setState({
                sort: `random`,
            })  
        }
        else {
            this.setState({
                sort: `animals.${e.target.value}`,
            })
        }
        // this.sendNewReq(this.state.currentAnimal);
    }

    orderList(e) {
        this.setState({
            order: e.target.value,
        })  
    }

    async sendNewReq(keyWord) {
        this.setState({
            loading: true,
        })

        const kw = keyWord;
        // example url
        // /public/animals/search/available/dogs/?limit=10&page=2&sort=+distance&fields[animals]=name&include=fosters
        // const url = `https://api.rescuegroups.org/v5/public/animals/search/available/cats/haspic/?sort=random&limit=5`;
        const url = `https://api.rescuegroups.org/v5/public/animals/search/available/${kw}/?limit=250&sort=${this.state.order}${this.state.sort}&include=fosters`;
        // &sort=+animals.ageString
        const h = new Headers();
        h.append('Authorization', 'RNA1fpYc');
        const req = new Request(url, {
            method: 'GET',
            headers: h,
            mode: 'cors',
        });

        const data = await fetch(req);
        const dataJsoned = await data.json()
        // console.log(dataJsoned.data[0].attributes);

        // console.log (typeof(dataJsoned.data));
        if (dataJsoned.data === undefined) {
            return;
        }

        const filtered = dataJsoned.data.map((animal) => {
            const obj = {};
            obj['Name'] = animal.attributes.name;
            obj['url'] = animal.attributes.pictureThumbnailUrl;            
            obj['Age'] = animal.attributes.ageString;
            obj['Breed'] = animal.attributes.breedString;
            obj['Fee'] = animal.attributes.adoptionFeeString;
            obj['Description'] = animal.attributes.descriptionText;
            obj['Activity Level'] = animal.attributes.activityLevel;
            obj['Coat Length'] = animal.attributes.coatLength;
            obj['Energy Level'] = animal.attributes.energyLevel;
            obj['Eye Color'] = animal.attributes.eyeColor;
            obj['Grooming Needs'] = animal.attributes.groomingNeeds;
            obj['New People Reaction'] = animal.attributes.newPeopleReaction;
            obj['Sex'] = animal.attributes.sex;
            obj['Qualities'] = animal.attributes.qualities;
            return obj; 
        })
        

        this.setState({
            currentAnimal: kw,
            loading: false,
            items: filtered,
        })
    }

    render() {
        return (
            <div className="main">
                {/* <button onClick={()=>console.log (this.props.menuToggled)}> bt</button> */}
                {(this.state.windowSize>=800)?<SideMenu sortlist={this.sortList} animals={this.state.animals} orderlist={this.orderList} sendnewreq={this.sendNewReq}/>
                :null}
                {(this.state.windowSize<=800)&&(this.props.menuToggled)?<div className="side-toggled"><SideMenu sortlist={this.sortList} animals={this.state.animals} orderlist={this.orderList} sendnewreq={this.sendNewReq}/></div>
                :null}                          
                {(this.state.loading) ? <div style={{display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center'}}><div className="loading"></div><h1 style={{marginTop: '30px'}}>loading...</h1></div> :
                <List sort={this.state.sort} items={this.state.items}/> }
            </div>
        )
    }
}

export default Main;