import React, { Component, useEffect, useState } from 'react';
import ReactPaginate from 'react-paginate';
import uniqid from 'uniqid';
import Details from './details';
import Items from './items';

function List(props, { itemsPerPage=9 }) {
    // We start with an empty list of items.
    const [currentItems, setCurrentItems] = useState(null);
    const [pageCount, setPageCount] = useState(0);
    const [detailsToggled, setDetailsToggled] = useState(false);

    const [itemOffset, setItemOffset] = useState(0);

    useEffect(() => {
        const endOffset = itemOffset + itemsPerPage;
        setCurrentItems(props.items.slice(itemOffset, endOffset));
        setPageCount(Math.ceil(props.items.length / itemsPerPage));
    }, [itemOffset, itemsPerPage, props.items, props.sort]);

    // function Items({ currentItems }) {
    //     return (
    //     <>
    //         {currentItems &&
    //         currentItems.map((item) => (
    //             <div key={uniqid()} className="card">
    //                 <img src={item.url} alt={item.Name} className="card-img"/>
    //                 <div className="card-texts">
    //                     <h3>Information</h3>
    //                     <div className='card-text'>
    //                         <p>{item.Name}</p>
    //                         <p>{item.Age}</p>
    //                         <p>{item.Breed}</p>
    //                         <p>{item.Fee}</p>
    //                     </div>
    //                     <div className='card-btn-group'>
    //                         <button onClick={viewDetails}> View details </button>
    //                     </div>
    //                 </div>
    //             </div>
    //         ))}
    //     </>
    //     );
    // }

    function viewDetails() {
        console.log ("clicked!");
        if (!detailsToggled) {
            setDetailsToggled(true);
        }
        else {
            setDetailsToggled(false);
        }
    } 

    const handlePageClick = (event) => {
        const newOffset = (event.selected * itemsPerPage) % props.items.length;
        setItemOffset(newOffset);
    };

    return (
        <div className='pagination'>
            <>
                <div>
                    <ReactPaginate
                        breakLabel="..."
                        nextLabel={<button className="next-previous-btns"> Next </button>}
                        onPageChange={handlePageClick}
                        pageRangeDisplayed={5}
                        pageCount={pageCount}
                        containerClassName='pagination-menu'
                        activeClassName='pagination-active-page'
                        previousLabel={<button className="next-previous-btns"> Previous </button>}
                        renderOnZeroPageCount={null}
                        breakClassName='1'
                        breakLinkClassName='2'
                        pageClassName='3'
                        pageLinkClassName='4'
                        activeLinkClassName='5'
                        previousClassName='6'
                        nextClassName='7'
                        previousLinkClassName='8'
                        nextLinkClassName='9'
                        disabledClassName='10'
                        disabledLinkClassName='11'	
                    />
                </div>
                <div className="pagination-items">
                    <Items currentItems={currentItems} />
                </div>
                <div>
                    <ReactPaginate
                        breakLabel="..."
                        nextLabel={<button className="next-previous-btns"> Next </button>}
                        onPageChange={handlePageClick}
                        pageRangeDisplayed={5}
                        pageCount={pageCount}
                        containerClassName='pagination-menu'
                        activeClassName='pagination-active-page'
                        previousLabel={<button className="next-previous-btns"> Previous </button>}
                        renderOnZeroPageCount={null}
                        breakClassName='1'
                        breakLinkClassName='2'
                        pageClassName='3'
                        pageLinkClassName='4'
                        activeLinkClassName='5'
                        previousClassName='6'
                        nextClassName='7'
                        previousLinkClassName='8'
                        nextLinkClassName='9'
                        disabledClassName='10'
                        disabledLinkClassName='11'	
                    />
                </div>
            </>
        </div>
    );
}
export default List;